*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

body {
  color: #4d5066;
  letter-spacing: 1px;
  font-family: Josefin Sans, sans-serif;
  font-size: 1rem;
  line-height: 1.4;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

header {
  background: url("bg-mobile-dark.ee9385c1.jpg") 0 0 / cover no-repeat;
  padding: 1rem;
}

@media (min-width: 800px) {
  header {
    background: url("bg-desktop-dark.bdf302f3.jpg") no-repeat;
  }
}

.changeheaderBg {
  background: url("bg-mobile-light.e1315d1e.jpg") 0 0 / cover no-repeat;
}

@media (min-width: 800px) {
  .changeheaderBg {
    background: url("bg-desktop-light.24aa7448.jpg") no-repeat;
  }
}

.nav {
  justify-content: space-between;
  padding: 2rem;
  display: flex;
}

.nav__heading {
  color: #fff;
  letter-spacing: 5px;
}

.nav__toggler {
  height: 1.8rem;
  cursor: pointer;
}

.nav .hide {
  display: none;
}

@media (min-width: 800px) {
  .nav {
    justify-content: space-around;
  }
}

.hero {
  margin-top: 2rem;
}

.hero__newTodo input {
  background-color: #0000;
  border: none;
  outline: none;
  padding-left: 6px;
  font-family: inherit;
  font-size: 11px;
}

.hero__newTodo input[type="text"] {
  color: #cacde8;
}

.circleBefore {
  position: relative;
}

.circleBefore:before {
  content: "";
  cursor: pointer;
  margin-bottom: 2px;
  height: 1.7rem;
  width: 1.7rem;
  border: 1px solid #cacde8;
  border-radius: 50%;
  margin-inline: 7px 5px;
  transition: border 1s;
  display: inline-block;
}

.circleBefore:hover:before {
  border: 1px solid #ccc;
}

main {
  height: 90vh;
  background-color: #161722;
  padding-inline: 1rem;
}

.col {
  width: 90%;
  background-color: #25273c;
  border-radius: 6px;
  margin-inline: auto;
  padding: 16px;
  font-size: 12px;
  display: flex;
}

@media (min-width: 800px) {
  .col {
    width: 50%;
  }
}

.col--sb {
  justify-content: space-between;
}

.col-added {
  border-bottom: 1px solid #393a4c;
  border-radius: 0;
  position: relative;
}

p {
  color: #cacde8;
}

ul li p {
  margin-left: 5px;
  padding-top: .5rem !important;
}

.clearTodos {
  border-radius: 0 0 6px 6px;
}

.check {
  content: url("icon-cross.49911680.svg");
  cursor: pointer;
  opacity: 1;
  margin-top: .4rem;
  transition: opacity 1s;
  position: absolute;
  right: 1rem;
}

.circle {
  content: "";
  cursor: pointer;
  margin-bottom: 2px;
  height: 1.7rem;
  width: 1.7rem;
  border: 1px solid #cacde8;
  border-radius: 50%;
  margin-inline: 7px 15px;
  transition: border .3s;
  display: inline-block;
  position: relative;
  left: -.5rem;
}

.bg {
  background-image: url("icon-check.0ef01779.svg"), linear-gradient(#57ddff, #c058f3);
  background-position: center;
  background-repeat: no-repeat;
}

.taskRemove {
  text-decoration: line-through;
}

.fixPosition {
  position: absolute;
  left: 3rem;
}

.completed, .all, .active, .clear-completed {
  cursor: pointer;
  transition: color .6s;
}

.completed:hover, .all:hover, .active:hover, .clear-completed:hover {
  color: #939495;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.bgWhite {
  background-color: #fafafa;
}

.clsLight {
  background-color: #fafafa;
  border: .1px solid #d2d3db;
}

.clsLight p {
  color: #939495;
}

